body {
  background: #e7e9eb;
  font-family: "Raleway" !important;
  box-sizing: border-box  !important ;
}

button {
  font-family: "Raleway light" !important;
}
button:focus {
  outline: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.calculator {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.3);
  border-radius: 8px;
  overflow: hidden;
}

.outputScreen {
  box-sizing: border-box;
  position: relative;
  text-align: right;
  font-size: 24px;
  color: #fff;
  padding: 100px 50px 0;
  background: #fff;
  height: 200px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: rgb(223,75,145);
  background: linear-gradient(39deg, rgba(223,75,145,1) 0%, rgba(244,166,60,1) 82%);
  
}

.apple {
  position: absolute;
  left: 10px;
  top: 10px;
  display: flex;
}
.apple span {
  display: block;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  margin: 0 5px;
  background: rgba(0, 0, 0, 0.3);
}

.cp {
  background: #eaeaea;
}
.number {
  background: #f5f5f5;
}
.output{
  font-size:24px;
}

.equals{
  background: rgb(223,75,145);
  background: linear-gradient(39deg, rgba(223,75,145,1) 0%, rgba(244,166,60,1) 82%);
  
  color:#fff !important;
}